<script
  charset="utf-8"
  src="https://map.qq.com/api/js?v=2.exp&key=R6BBZ-U7WKR-JEOWG-WKPR7-6Z5W5-RSBMW&libraries=convertor"
></script>
<template>
  <v-app id="inspire">
    <v-app-bar app color="indigo" dark>
      <v-toolbar-title>出行小助手</v-toolbar-title>
    </v-app-bar>

    <v-content>
      <v-container class="fill-height" fluid>
        <v-row align="center" justify="center dense">
          <v-col cols="12" sm="8" md="4">
            <v-card
              class="mx-auto mt-5 mb-5"
              max-width="400"
              v-for="value in plan"
              :key="value.id"
              flat
            >
              <v-img
                class="white--text align-end"
                height="200px"
                src="./assets/timg.jpeg"
              >
                <v-card-title>{{ address }}公交路线 </v-card-title>
              </v-img>
              <v-card-text class="text--primary">
                <ul>
                  <li v-for="action in value.actions" :key="action.id">
                    {{ action.instructions }}
                  </li>
                </ul>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="orange" text v-on:click="back">重新搜索</v-btn>
              </v-card-actions>
            </v-card>

            <v-card-text class="elevation-12" id="input" v-if="flag">
              <v-form>
                <v-text-field
                  class="ma-2"
                  v-model="address"
                  label="目的地"
                  required
                ></v-text-field>
                <v-select
                  class="ma-2"
                  v-model="policy"
                  label="搜索方式"
                  :items="items"
                ></v-select>
              </v-form>
              <v-btn
                depressed
                large
                color="primary"
                id="btn-go"
                v-on:click="search"
                >GO</v-btn
              >
            </v-card-text>
          </v-col>
        </v-row>
      </v-container>
    </v-content>

    <v-footer color="indigo" app>
      <span class="white--text">&copy; 2019</span>
    </v-footer>
  </v-app>
</template>

<script>
export default {
  props: {
    source: String
  },
  data: () => ({
    flag: true,
    address: null,
    local: new qq.maps.LatLng(32.021872, 118.856977),
    city: null,
    policy: "LEAST_TIME",
    plan: null,
    items: [
      { text: "时间最短", value: "LEAST_TIME" },
      { text: "最少换乘", value: "LEAST_TRANSFER" },
      { text: "最少走路", value: "LEAST_WALKING" },
      { text: "不乘地铁", value: "NO_SUBWAY" }
    ]
  }),
  methods: {
    back: function() {
      this.flag = true;
      this.plan = null;
    },
    getLocal: function() {
      var _self = this;
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(getPosition);
      } else {
        alert("Geolocation is not supported by this browser.");
      }
      function getPosition(position) {
        var lat = position.coords.latitude;
        var lng = position.coords.longitude;
        //调用地图命名空间中的转换接口   type的可选值为 1:GPS经纬度，2:搜狗经纬度，3:百度经纬度，4:mapbar经纬度，5:google经纬度，6:搜狗墨卡托
        qq.maps.convertor.translate(new qq.maps.LatLng(lat, lng), 1, function(
          res
        ) {
          //取出经纬度并且赋值
          //console.log(res);
          _self.local = res[0];
        });
      }
    },
    getCity: function() {
      var _self = this;
      var citylocation = null;
      citylocation = new qq.maps.CityService();
      //请求成功回调函数
      citylocation.setComplete(function(result) {
        _self.city = result.detail.name;
      });
      //请求失败回调函数
      citylocation.setError(function() {
        alert("出错了");
      });
      citylocation.searchLocalCity();
    },

    search: function(event) {
      var _self = this;
      var m = null;
      var p = null;

      var transferService = new qq.maps.TransferService({ map: m, panel: p });
      transferService.setComplete(function(result) {
        //如果service返回的结果类型为qq.maps.ServiceResultType.MULTI_DESTINATION（起点或终点位置不唯一），弹出提示信息
        if (result.type == qq.maps.ServiceResultType.MULTI_DESTINATION) {
          alert("起终点不唯一");
        } else {
          _self.plan = result.detail.plans;
          _self.flag = false;
          console.log(result.detail.plans);
        }
      });
      //设置检索失败回调函数
      transferService.setError(function(error) {
        if (error == qq.maps.ServiceErrorType.Error) {
          alert("服务器异常");
        }
      });
      var start = _self.local;
      var end = _self.address;
      var poli = _self.policy;
      transferService.setPolicy(qq.maps.TransferPolicy[poli]);
      transferService.setLocation(_self.city);
      transferService.search(start, end);
    }
  },
  mounted() {
    this.getCity();
    this.getLocal();
  }
};
</script>
